import { Component, Input, OnInit, ViewEncapsulation } from '@angular/core';
import { NgxLoadingConfig } from 'ngx-loading';

@Component({
  selector: 'app-loading',
  templateUrl: './app-loading.component.html',
  styleUrls: ['./app-loading.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class AppLoadingComponent implements OnInit {
  @Input() show: boolean = false;
  @Input() backdrop: boolean = false;
  @Input() color: string = 'var(--primary)';
  @Input() size: string = '18px';

  public config: NgxLoadingConfig = {};

  constructor() { }

  ngOnInit(): void {
    this.config.primaryColour = this.color;
    this.config.secondaryColour = this.color;
    this.config.tertiaryColour = this.color;
    this.config.backdropBackgroundColour = this.backdrop
      ? 'rgba(255, 255, 255, 0.5)'
      : 'rgba(255, 255, 255, 0)';
  }
}
