import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { PluginsService } from 'src/app/plugins.service';
import { AuthService } from 'src/app/services/auth.service';
import { MENU_SIDEBAR, MenuSidebar } from './menu-sidebar.data';

@Component({
  selector: 'app-side-bar',
  templateUrl: './app-side-bar.component.html',
  styleUrls: ['./app-side-bar.component.scss']
})
export class AppSideBarComponent implements OnInit {
  public menuItems: MenuSidebar[] = [];

  constructor(
    private plugins: PluginsService,
    private router: Router,
    private authService: AuthService
  ) {}

  ngOnInit(): void {
    this.loadMenu();
  }

  public activeLink(item: MenuSidebar): boolean {
    return this.plugins.getActiveLink(item, this.router.url);
  }

  public toggleMenu(): void {
    if ($(window).width() < 1300) {
      this.plugins.toggleMenu();
    }
  }

  private loadMenu(): void {
    const user = this.authService.getUser();

    const userTypeFilter = (children: MenuSidebar[]) => {
      return children.filter(c => {
        if (c.userTypes && c.userTypes.length > 0 && !c.userTypes.includes(user.tipo)) {
          return false;
        }
        if (c.children) {
          c.children = userTypeFilter(c.children);
        }
        return true;
      });
    }

    this.menuItems = userTypeFilter(MENU_SIDEBAR);
  }
}
