import { Component, ElementRef, Input, OnInit, ViewChild } from '@angular/core';

@Component({
  selector: 'app-button',
  templateUrl: './app-button.component.html',
  styleUrls: ['./app-button.component.scss']
})
export class AppButtonComponent implements OnInit {
  @Input() type: ButtonType = 'submit';
  @Input() color: ButtonColor = 'primary';
  @Input() size: ButtonSize = 'default';
  @Input() rounded: ButtonRounded = 'pill';
  @Input() form?: string;
  @Input() outline: boolean = false;
  @Input() loading: boolean = false;
  
  @ViewChild('button', { static: true }) button: ElementRef<HTMLButtonElement>;

  public classes: ButtonClasses = { btn: true };

  constructor() {
  }

  public ngOnInit(): void {
    if (this.outline === true) {
      this.classes[`btn-outline-${this.color}`] = true;
    } else {
      this.classes[`btn-${this.color}`] = true;
    }

    if (this.size === 'large') {
      this.classes['btn-lg'] = true;
    } else if (this.size === 'small') {
      this.classes['btn-sm'] = true;
    }

    if (this.rounded !== 'default') {
      this.classes[`rounded-${this.rounded}`] = true;
    } 

    if (this.form && this.type == 'submit') {
      this.button.nativeElement.setAttribute('form', this.form);
    }
  }
}

type ButtonType = 'button' | 'submit' | 'reset';

type ButtonColor = 'primary' 
  | 'secondary' 
  | 'success' 
  | 'danger'
  | 'info'
  | 'danger'
  | 'warning';

type ButtonSize = 'large' | 'small' | 'default';

type ButtonRounded = 'pill' | 'circle' | 'default';

type ButtonClasses = { [key: string]: string | number | boolean };